import React from "react";

const About = React.lazy(() => import("./views/dashboard/Dashboard"));
const Contact = React.lazy(() => import("./views/contact/contact"));
const Donnees = React.lazy(() => import("./views/donnees/donneesAntoine"));
const Devices = React.lazy(() => import("./views/devices/devices"));
const System = React.lazy(() => import("./views/devices/ApexChart"));
const ViewDevice = React.lazy(() => import("./views/devices/ApexChart2"));
const Users = React.lazy(() => import("./views/users/Users"));
const User = React.lazy(() => import("./views/users/User"));
const Dashboard = React.lazy(() => import("./views/bigPicture/BigPicture"));

const routes = [
  { path: "/", exact: true, name: "Home" },
  { path: "/home", name: "About", component: About, admin: false },
  { path: "/contact", name: "Contact", component: Contact, admin: false },
  { path: "/data", name: "Dashboard", component: System, admin: true },
  { path: "/batterie", name: "Données", component: ViewDevice, admin: true },
  { path: "/panneauSolaire", name: "Données", component: ViewDevice, admin: true },
  { path: "/mppt", name: "mppt", component: ViewDevice, admin: true },
  { path: "/TableauDonnées", name: "Sommaire", component: Donnees, admin: true },
  { path: "/chargeur", name: "Données", component: ViewDevice, admin: true },
  { path: "/sortie1", name: "Données", component: ViewDevice, admin: true },
  { path: "/onduleur", name: "Données", component: ViewDevice, admin: true },
  { path: "/sortie2", name: "Graphiques", component: ViewDevice, admin: true },
  { path: "/devices", name: "Appareils", component: Devices, admin: true },
];

export default routes;
