import React, { Suspense } from "react";
import { Redirect, Route, Switch } from "react-router-dom";
import { CContainer, CFade } from "@coreui/react";
import cookies from "js-cookie";
import { getDevices } from "../store/store";
import store from "../store/store";
import { useSelector, useDispatch } from "react-redux";
import routes from "../routes";
const Login = React.lazy(() => import("../views/pages/login/Login"));

const { login, verify } = require("../endpoint/users");
// routes config

const loading = (
  <div className="pt-3 text-center">
    <div className="sk-spinner sk-spinner-pulse"></div>
  </div>
);

const TheContent = () => {
  const isLog = useSelector((state) => state.isLog);
  const dispatch = useDispatch();

  const onLogin = (username, password) => {
    console.log("result");
    login({ username, password })
      .then((result) => {
        if (result) {
          cookies.set("jwt", result.data);
          dispatch({ type: "login/isLog", isLog: true });
          store.dispatch(getDevices);
        }
      })
      .catch((e) => console.log(e));
  };

  return (
    <main>
      <CContainer fluid>
        <Suspense fallback={loading}>
          <Switch>
            {routes.map((route, idx) => {
              if (route.admin) {
                if (!isLog) {
                  return (
                    <Route
                      path="/"
                      name="login"
                      render={(props) => <Login {...props} onLogin={(username, password) => onLogin(username, password)} />}
                    />
                  );
                } else {
                  return (
                    route.component && (
                      <Route
                        key={idx}
                        path={route.path}
                        exact={route.exact}
                        name={route.name}
                        render={(props) => (
                          <CFade>
                            <route.component {...props} />
                          </CFade>
                        )}
                      />
                    )
                  );
                }
              } else {
                return (
                  route.component && (
                    <Route
                      key={idx}
                      path={route.path}
                      exact={route.exact}
                      name={route.name}
                      render={(props) => (
                        <CFade>
                          <route.component {...props} />
                        </CFade>
                      )}
                    />
                  )
                );
              }
            })}
            <Redirect from="/" to="/home" />
          </Switch>
        </Suspense>
      </CContainer>
    </main>
  );
};

export default React.memo(TheContent);
